import React, { useEffect, useRef, useState } from "react";
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Stepper,
  Step,
  StepLabel,
  Typography,
  Divider,
} from "@mui/material";
import { Formik, Field, Form } from "formik";
import * as Yup from "yup";
import Loading from "app/components/Loading";
import { Themecolors } from "api/Colors";
import { renderFormFields } from "app/user/FormFields";
import { useLocation } from "react-router-dom";
import appStore from "app/mobxStore/AppStore";
import { v4 as uuidv4 } from "uuid";
import { Delete } from "@mui/icons-material";
import AddIcon from "@mui/icons-material/Add";

interface FormField {
  label: string;
  type: string;
  name: string;
}

interface FormGroup {
  groupName: string;
  repeated: boolean;
  form: FormField[];
}

interface FormManagementFormProps {
  Data: any;
  onSubmit: (values: any) => void;
  showCloneButton?: boolean;
}

const FormManagementForm: React.FC<FormManagementFormProps> = ({
  Data,
  onSubmit,
  showCloneButton = true,
}) => {
  const FormRef = useRef(null);
  const [formData, setFormData] = useState<any>([]);
  const [step, setStep] = useState(0);
  const [documentType, setDocumentType] = useState(0);
  const location = useLocation();
  const currentPath = location.pathname;
  const params = currentPath.split("/");
  const [selections, setSelections] = useState([]);
  const [msgDialog, setMsgDialog] = useState(false);
  const [documentTagType, setDocumentTagType] = useState("1");
  const [showFormViewer, setShowFormViewer] = useState(false);
  const [formHasErrors, setFormHasErrors] = useState(false);

  useEffect(() => {
    if (Data && Data.sections) {
      setFormData(Data.sections);
      setDocumentTagType(Data.documentTagType);
      setSelections(Data.selections || []);
    }
  }, [Data]);

  const getValidationFromStr = (str: any) => {
    let validation = Yup.string();
    const validators = str.split(",");

    for (let i = 0; i < validators.length; i++) {
      if (validators[i] === "req") {
        validation = validation.required("Required");
      } else if (validators[i] === "email") {
        validation = validation.email("Invalid email address");
      } else if (validators[i] === "min") {
        const num = parseInt(validators[i + 1]);
        if (!isNaN(num)) {
          validation = validation.min(
            num,
            `Must be at least ${num} characters`
          );
          i++; // skip the next loop since it's a parameter for 'min'
        }
      }
      // Add other validators as needed...
    }
    return validation;
  };

  const generateValidationSchema = (sections) => {
    const schema = {};

    sections.forEach((section) => {
      const validationSchema = section.validationSchema;
      for (const key in validationSchema) {
        schema[key] = getValidationFromStr(validationSchema[key]);
      }
    });

    return Yup.object(schema);
  };
  const validationSchema = generateValidationSchema(formData);
  const initialValues = formData[step]?.initialValues ?? {};
  const formDetails: any[] = formData[step]?.formDetails ?? [];

  const sections = formData.length;

  const handleNext = async () => {
    if (FormRef.current) {
      const formik = FormRef.current;

      const errors = await formik.validateForm();
      if (stepHasErrors(errors, step, formData)) {
        setFormHasErrors(true);
      } else {
        setFormHasErrors(false);
      }
    }
    if (step < sections - 1) setStep(step + 1);
  };

  const onBack = () => {
    if (step > 0) setStep(step - 1);
  };
  const hanldeClose = () => {
    // navigate("/dashboard/default");
  };

  const getSelections = (order) => {
    const splitOrder = String(order).split("-");
    if (selections) {
      const questionIndex = +splitOrder[0];
      const res = selections.reduce((acc, curr) => {
        if (curr.questions.includes(questionIndex)) {
          return [...acc, ...curr.options];
        }
        return acc;
      }, []);
      return res;
    } else return [];
  };

  const handleAddNewRow = (sectionIndex, groupIndex) => {
    // Clone the section
    const newSections = [...formData];
    const repeatedForm = newSections[sectionIndex].formDetails[groupIndex].form;
    const currentIndex = newSections[sectionIndex].formDetails.length - 1;

    // Add the repeated form group
    newSections[sectionIndex].formDetails.push({
      groupName: newSections[sectionIndex].formDetails[groupIndex].groupName,
      repeated: true,
      form: repeatedForm.map((item, index) => ({
        ...item,
        order: `${item.order}-${currentIndex}`,
      })),
    });

    setFormData(newSections);
  };

  const handleDeleteNewRow = (sectionIndex, groupIndex) => {
    const newSections = [...formData];
    newSections[sectionIndex].formDetails[groupIndex] = { form: [] };

    setFormData(newSections);
  };

  const stepHasErrors = (errors, currentStep, formData) => {
    const currentFormDetails = formData[currentStep]?.formDetails ?? [];
    const hasErrors = currentFormDetails.some((group) =>
      group.form.some((item) => errors[item.order])
    );
    setFormHasErrors(Object.entries(errors).length > 0);

    // if (hasErrors) {
    //   setIsSubmitting(false);
    // }
    return hasErrors;
  };

  const handleSubmit = () => {
    if (FormRef.current && !formHasErrors) {
      FormRef.current.submitForm();
    } else if (formHasErrors) {
      appStore.showToast("Some fields are required", "warning");
    }
  };

  const showButton = sections === 1 || step === sections - 1;

  return (
    <>
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          backgroundColor: "#f4f6f8",
          width: "100%",
          height: "100%",
        }}
      >
        <Card
          sx={{
            width: "100%",
            height: "100%",
            borderRadius: 0,
            display: "flex",
            flexDirection: "column",
            overflow: "hidden",
            boxSizing: "border-box",
          }}
        >
          <Grid
            padding={"7px"}
            justifyContent={"space-between"}
            display={"flex"}
            width={"100%"}
            style={{
              backgroundColor: Themecolors.F_bg2,
              color: Themecolors.F_text1,
              maxHeight: "6vh",
            }}
          >
            {step >= 1 && (
              <Button
                type="button"
                variant="outlined"
                onClick={onBack}
                sx={{
                  borderColor: Themecolors.Button1,
                  backgroundColor: Themecolors.Button1,
                  color: Themecolors.Button2,
                  "&:hover": {
                    backgroundImage: Themecolors.B_hv1,
                    borderColor: Themecolors.Button1,
                  },
                }}
              >
                Back
              </Button>
            )}
            <Typography
              align="left"
              sx={{
                display: "flex",
                justifyContent: "center",
                fontSize: "1.25em",
              }}
            >
              {formData[step]?.sectionName ?? ""}
            </Typography>
            {step < sections - 1 && (
              <Button
                type="button"
                variant="outlined"
                color="primary"
                onClick={handleNext}
                sx={{
                  borderColor: Themecolors.Button1,
                  backgroundColor: Themecolors.Button1,
                  color: Themecolors.Button2,
                  "&:hover": {
                    backgroundImage: Themecolors.B_hv1,
                    borderColor: Themecolors.Button1,
                  },
                }}
              >
                Next
              </Button>
            )}
          </Grid>
          <CardContent
            sx={{
              flexGrow: 1,
              padding: 0,
              display: "flex",
              flexDirection: "column",
              overflow: "hidden",
              boxSizing: "border-box",
            }}
          >
            <Box
              sx={{
                width: "100%",
                height: "100%",
                boxSizing: "border-box",
              }}
            >
              <Formik
                innerRef={FormRef}
                initialValues={initialValues}
                validationSchema={validationSchema}
                key={documentType}
                onSubmit={async (values, {}) => {
                  const formValues = { ...values };
                }}
              >
                {({
                  errors,
                  submitCount,
                  isSubmitting,
                  values,
                  handleSubmit,
                  handleChange,
                }) => (
                  <Box
                    sx={{
                      display: "flex",
                      flexDirection: "column",
                      height: "100%",
                      width: "100%",
                    }}
                  >
                    <Box
                      sx={{
                        flexGrow: 1,
                        overflowY: "auto",
                        overflowX: "hidden",
                        boxSizing: "border-box",
                      }}
                    >
                      <Form
                        style={{
                          display: "flex",
                          flexDirection: "column",
                        }}
                      >
                        {formDetails.map((group, index) => {
                          if (group.form.length === 0) {
                            return null;
                          }

                          const sectionColumns = formData[step]?.columns || "1";
                          const isMultiColumn = sectionColumns === "2";
                          const splitIndex = Math.ceil(group.form.length / 2);
                          const formColumn1 = isMultiColumn
                            ? group.form.slice(0, splitIndex)
                            : group.form;
                          const formColumn2 = group.form.slice(splitIndex);

                          if (group.display === "horizontal") {
                            return (
                              <Grid
                                container
                                spacing={0}
                                key={index}
                                sx={{
                                  pt: "8px",
                                  width: "100%",
                                  m: 0,
                                }}
                              >
                                <Grid item xs={12}>
                                  <Box
                                    sx={{
                                      backgroundColor: "#f5f5f5",
                                      paddingLeft: 0,
                                    }}
                                  >
                                    <Typography
                                      variant="h6"
                                      sx={{
                                        color: "#333",
                                        p: "5px",
                                      }}
                                    >
                                      {group.groupName}
                                    </Typography>
                                  </Box>
                                </Grid>
                                <Grid item xs={12}>
                                  <Grid
                                    container
                                    spacing={1}
                                    sx={{ width: "100%", m: 0, pr: 1 }}
                                  >
                                    {group.form.map((item, i) => (
                                      <Grid
                                        item
                                        xs={12 / group.form.length}
                                        key={item.order || i}
                                        sx={{ width: "100%" }}
                                      >
                                        {renderFormFields(
                                          item,
                                          i,
                                          handleChange,
                                          values,
                                          errors,
                                          getSelections,
                                          documentTagType
                                        )}
                                      </Grid>
                                    ))}
                                  </Grid>
                                </Grid>
                              </Grid>
                            );
                          }
                          return (
                            <Box
                              key={index}
                              marginTop={"10px"}
                              borderBottom={1}
                              borderColor="grey.300"
                              sx={{
                                width: "100%",
                              }}
                            >
                              {group.repeated && index == 0 && (
                                <>
                                  <Box
                                    display="flex"
                                    justifyContent="flex-end"
                                    alignItems="center"
                                  >
                                    <Button
                                      variant="outlined"
                                      color="primary"
                                      endIcon={<AddIcon />}
                                      onClick={() =>
                                        handleAddNewRow(step, index)
                                      }
                                      style={{
                                        marginRight: "16px",
                                        height: "2.2em",
                                      }} // adjust as needed
                                    >
                                      Add New Row
                                    </Button>
                                  </Box>
                                  <Box
                                    sx={{
                                      backgroundColor: "#f5f5f5", // Light gray background for a modern look
                                      padding: "6px",
                                    }}
                                  >
                                    <Typography
                                      variant="h6"
                                      align="left"
                                      sx={{
                                        color: "#333",
                                      }}
                                    >
                                      {group.groupName}
                                    </Typography>
                                  </Box>
                                </>
                              )}
                              {!group.repeated && (
                                <Box
                                  sx={{
                                    backgroundColor: "#f5f5f5", // Light gray background for a modern look
                                    padding: "6px",
                                  }}
                                >
                                  <Typography
                                    variant="h6"
                                    align="left"
                                    sx={{
                                      color: "#333",
                                    }}
                                  >
                                    {group.groupName}
                                  </Typography>
                                </Box>
                              )}

                              <Grid
                                container
                                spacing={1}
                                style={{
                                  boxSizing: "border-box",
                                  marginLeft: 0,
                                  paddingRight: isMultiColumn ? "0px" : "16px",
                                }}
                              >
                                <Box
                                  sx={{
                                    width: "100%",
                                    display: "flex",
                                    justifyContent: "end",
                                  }}
                                >
                                  {group.repeated && index > 0 && (
                                    <Delete
                                      sx={{
                                        alignItems: "end",
                                        mr: isMultiColumn ? "16px" : "0",
                                      }}
                                      onClick={() =>
                                        handleDeleteNewRow(step, index)
                                      }
                                    />
                                  )}
                                </Box>
                                <Grid item xs={isMultiColumn ? 5.5 : 12}>
                                  {formColumn1.map((item, i) =>
                                    renderFormFields(
                                      item,
                                      i,
                                      handleChange,
                                      values,
                                      errors,
                                      getSelections,
                                      documentTagType
                                    )
                                  )}
                                </Grid>
                                {isMultiColumn && (
                                  <>
                                    <Divider
                                      orientation="vertical"
                                      flexItem
                                      variant="middle"
                                      style={{
                                        paddingLeft: 6,
                                        paddingRight: 3,
                                        color: "red",
                                      }}
                                    />
                                    <Grid
                                      item
                                      xs={6}
                                      style={{
                                        paddingLeft: "10px",
                                      }}
                                    >
                                      {formColumn2.map((item, i) =>
                                        renderFormFields(
                                          item,
                                          i,
                                          handleChange,
                                          values,
                                          errors,
                                          getSelections,
                                          documentTagType
                                        )
                                      )}
                                    </Grid>
                                  </>
                                )}
                              </Grid>
                            </Box>
                          );
                        })}
                      </Form>
                    </Box>

                    <Box
                      sx={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        position: "absolute",
                        left: 0,
                        bottom: 0,
                        width: "100%",
                        backgroundColor: "#FFF",
                        boxSizing: "border-box",
                        overflow: "hidden",
                      }}
                    >
                      {sections > 1 && (
                        <Box
                          sx={{
                            paddingY: "14px",
                            width: "98%",
                            borderTop: "1px solid #ccc",
                          }}
                        >
                          <Stepper
                            activeStep={step}
                            alternativeLabel
                            sx={{
                              backgroundColor: "#FFF",
                              width: "100%",
                              "& .MuiStepIcon-root": {
                                color: Themecolors.Button_bg2,
                                "&.Mui-active": {
                                  color: Themecolors.Button_bg1,
                                },
                                "&.Mui-completed": {
                                  color: Themecolors.main_bg1,
                                },
                                "&.Mui-error": {
                                  color: "#FF3D57",
                                },
                              },
                              "& .MuiStepLabel-root": {
                                "&.Mui-error": {
                                  color: Themecolors.Error_Color,
                                },
                              },
                            }}
                          >
                            {Array.from({ length: sections }, (_, i) => (
                              <Step
                                key={i}
                                onClick={() => {
                                  setStep(i);
                                }}
                                style={{
                                  cursor: "pointer",
                                }}
                              >
                                <StepLabel
                                  error={stepHasErrors(errors, i, formData)}
                                >
                                  {/* {`Step  ${i + 1}`} */}
                                </StepLabel>
                              </Step>
                            ))}
                          </Stepper>{" "}
                        </Box>
                      )}
                    </Box>
                  </Box>
                )}
              </Formik>
            </Box>
          </CardContent>
        </Card>
      </Box>
    </>
  );
};

export default FormManagementForm;
