import React, { useState, useEffect } from "react";
import {
  Box,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Switch,
  TextField,
  Typography,
} from "@mui/material";
import Manditory from "./Manditory ";
interface Question {
  order: string;
  label: string;
  description: string;
  value?: string;
  type?: string;
  dropdownValue?: string;
}

interface Group {
  name: string;
  repeated?: boolean;
  questions: Question[];
  display?: string;
}

interface Section {
  sectionName: string;
  selectedGroups: Group[];
  columns?: string;
}

interface SectionDetailsProps {
  handleValidationChange: (
    sectionIdx: number,
    groupIdx: number,
    questionIdx: number,
    validation: any
  ) => void;

  data?: any;
  sections: Section[];
  selectedSectionIndex: number | null;
  selectedGroupIndex: number | null;
  selectedQuestionIndex: number | null;
  handleSectionNameChange: (sectionIdx: number, name: string) => void;
  handleGroupNameChange: (
    sectionIdx: number,
    groupIdx: number,
    name: string
  ) => void;
  handleGroupRepeatToggle: (
    sectionIdx: number,
    groupIdx: number,
    value: boolean
  ) => void;
  handleQuestionLabelChange: (
    sectionIdx: number,
    groupIdx: number,
    questionIdx: number,
    value: string
  ) => void;
  handleQuestionDescriptionChange: (
    sectionIdx: number,
    groupIdx: number,
    questionIdx: number,
    value: string
  ) => void;
  handleQuestionDefaultValueChange: (
    sectionIdx: number,
    groupIdx: number,
    questionIdx: number,
    value: string
  ) => void;

  handleQuestionDropDownChange: (
    sectionIdx: number,
    groupIdx: number,
    questionIdx: number,
    value: string
  ) => void;
  handleSectionColumnsChange: (sectionIdx: number, columns: string) => void;
  handleGroupDisplayChange: (
    sectionIdx: number,
    groupIdx: number,
    value: string
  ) => void;
}

const RenderQuestionFields: React.FC<SectionDetailsProps> = ({
  handleGroupDisplayChange,
  handleValidationChange,
  data,
  sections,
  selectedSectionIndex,
  selectedGroupIndex,
  selectedQuestionIndex,
  handleSectionNameChange,
  handleGroupNameChange,
  handleGroupRepeatToggle,
  handleQuestionLabelChange,
  handleQuestionDescriptionChange,
  handleQuestionDefaultValueChange,
  handleQuestionDropDownChange,
  handleSectionColumnsChange,
}) => {
  const [selectedDropdownValue, setSelectedDropdownValue] = useState("");

  const question =
    selectedSectionIndex !== null &&
    selectedGroupIndex !== null &&
    selectedQuestionIndex !== null
      ? sections[selectedSectionIndex]?.selectedGroups[selectedGroupIndex]
          ?.questions[selectedQuestionIndex]
      : null;

  useEffect(() => {
    if (!question || !data?.documentTypeObject?.selections) return;

    const questionOrder = Number(question.order);

    const matchedSelection = data.documentTypeObject.selections.find(
      (sel: any) =>
        Array.isArray(sel.questions) && sel.questions.includes(questionOrder)
    );

    if (matchedSelection) {
      setSelectedDropdownValue(matchedSelection.name);
    } else {
      setSelectedDropdownValue(question.dropdownValue || "");
    }
  }, [question, data]);

  if (selectedSectionIndex === null) {
    return <Typography variant="body1">No section selected</Typography>;
  }

  const section = sections[selectedSectionIndex];

  if (selectedGroupIndex === null) {
    return (
      <Box sx={{ padding: "16px" }}>
        <TextField
          label="Section Name"
          fullWidth
          variant="outlined"
          value={section?.sectionName}
          onChange={(e) =>
            handleSectionNameChange(selectedSectionIndex, e.target.value)
          }
          sx={{ mb: 2 }}
        />
        <FormControl fullWidth variant="outlined">
          <InputLabel>Columns</InputLabel>
          <Select
            label="Select Option"
            value={section?.columns ?? ""}
            onChange={(e) =>
              handleSectionColumnsChange(selectedSectionIndex, e.target.value)
            }
          >
            <MenuItem value="1">1</MenuItem>
            <MenuItem value="2">2</MenuItem>
          </Select>
        </FormControl>
      </Box>
    );
  }

  const group = section?.selectedGroups[selectedGroupIndex];

  if (selectedQuestionIndex === null) {
    return (
      <Box sx={{ padding: "16px" }}>
        <TextField
          label="Group Name"
          fullWidth
          variant="outlined"
          value={group?.name}
          onChange={(e) =>
            handleGroupNameChange(
              selectedSectionIndex,
              selectedGroupIndex,
              e.target.value
            )
          }
        />
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            marginTop: 2,
          }}
        >
          <Typography variant="body1">Repeat Group</Typography>
          <Switch
            checked={group?.repeated ?? false}
            onChange={(e) =>
              handleGroupRepeatToggle(
                selectedSectionIndex,
                selectedGroupIndex,
                e.target.checked
              )
            }
          />
        </Box>
        <FormControl fullWidth variant="outlined" sx={{ mt: 2 }}>
          <InputLabel>Display</InputLabel>
          <Select
            label="Select Option"
            value={group?.display ?? ""}
            onChange={(e) =>
              handleGroupDisplayChange(
                selectedSectionIndex,
                selectedGroupIndex,
                e.target.value
              )
            }
          >
            <MenuItem value="vertical">Vertical</MenuItem>
            <MenuItem value="horizontal">Horizontal</MenuItem>
          </Select>
        </FormControl>
      </Box>
    );
  }

  // Render question fields when question selected
  return (
    <>
      <Box sx={{ position: "relative" }}>
        <Box
          sx={{
            display: "inline-flex",
            alignItems: "center",
            justifyContent: "center",
            borderRadius: "10%",
            bgcolor: "#757575",
            color: "#ffffff",
            fontWeight: "bold",
            fontSize: "0.8em",
            padding: "3px 10px",
            float: "right",
            mb: 1,
            mr: 2,
            width: "auto",
            height: "auto",
          }}
        >
          {question?.order}
        </Box>

        <Box sx={{ padding: "16px" }}>
          <TextField
            label="Question"
            fullWidth
            variant="outlined"
            value={question?.label}
            onChange={(e) =>
              handleQuestionLabelChange(
                selectedSectionIndex,
                selectedGroupIndex,
                selectedQuestionIndex,
                e.target.value
              )
            }
            sx={{ mb: 2 }}
          />
          <TextField
            label="Description"
            fullWidth
            variant="outlined"
            value={question?.description}
            onChange={(e) =>
              handleQuestionDescriptionChange(
                selectedSectionIndex,
                selectedGroupIndex,
                selectedQuestionIndex,
                e.target.value
              )
            }
            sx={{ mb: 2 }}
          />
          <TextField
            label="Default Value"
            fullWidth
            variant="outlined"
            value={question?.value || ""}
            onChange={(e) =>
              handleQuestionDefaultValueChange(
                selectedSectionIndex,
                selectedGroupIndex,
                selectedQuestionIndex,
                e.target.value
              )
            }
            sx={{ mb: 2 }}
          />
          {["1", "8", "3", "4"].includes(question?.type) && (
            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel>Select Option</InputLabel>
              <Select
                value={selectedDropdownValue}
                label="Select Option"
                onChange={(e) => {
                  const newValue = e.target.value;
                  setSelectedDropdownValue(newValue);
                  handleQuestionDropDownChange(
                    selectedSectionIndex!,
                    selectedGroupIndex!,
                    selectedQuestionIndex!,
                    newValue
                  );
                }}
              >
                {(data?.documentTypeObject?.selections ?? []).map(
                  (selection: any, index: number) => (
                    <MenuItem key={index} value={selection.name}>
                      {selection.name}
                    </MenuItem>
                  )
                )}
              </Select>
            </FormControl>
          )}
        </Box>
        <Manditory
          data={data}
          questionIndex={selectedQuestionIndex!}
          questionOrder={question?.order}
          onValidationChange={(validation) => {
            handleValidationChange(
              selectedSectionIndex!,
              selectedGroupIndex!,
              selectedQuestionIndex!,
              validation[question.order]
            );
          }}
        />
      </Box>
    </>
  );
};

export default RenderQuestionFields;
