import * as React from "react";
import { Box, Typography } from "@mui/material";
import { ErrorMessage, Field } from "formik";
import TextInput from "app/formComponents/TextInput";
import CustomTextarea from "app/formComponents/CustomTextArea";
import CheckboxGroup from "app/formComponents/CheckBoxGroup";
import RadioButton from "app/formComponents/RadioButton";
import CustomUploadFile from "app/formComponents/CustomUploadFile";
import NumericInput from "app/formComponents/NumericInput";
import Dropdown from "app/formComponents/DropDown";
import CustomDatePicker from "app/formComponents/CustomDatePicker";
import SingleSelectionButton from "./SingleSelectionComponent";

interface TriggerType {
  id: number;
  type: string;
  value: string;
  compareId: string;
}

export const renderFormFields = (
  item,
  i,
  handleChange,
  values,
  errors,
  getSelections,
  documentTagTypeonChange
) => {
  const handleselectionvalue = (
    selectedOption: { name: string; id: string },
    fieldName: string
  ) => {
    handleChange({ target: { name: fieldName, value: selectedOption.id } });
  };

  const shouldFieldBeVisible = (trigger: TriggerType | null) => {
    if (!trigger) return true;
    const currentValues = values[trigger.compareId];
    const selectedvalues = item.trigger?.value.split(",");
    return selectedvalues.includes(currentValues);
  };

  if (item.trigger && !shouldFieldBeVisible(item.trigger)) {
    return null;
  }

  switch (+item.attributeType) {
    case 1: // Textbox
      return (
        <Box key={item.order} marginBottom={2}>
          <Typography
            variant="caption"
            align="left"
            gutterBottom
            style={{ fontSize: "0.60rem", color: "#6b6b6b" }}
          >
            {item.attributeDescription}
          </Typography>
          <Field
            name={item.order}
            label={item.attributeName}
            component={TextInput}
          />

          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );
    case 2: //Description box
      return (
        <Box key={item.order} marginBottom={2}>
          <Typography
            variant="caption"
            align="left"
            gutterBottom
            style={{
              fontSize: "0.60rem",
              color: "#6b6b6b",
            }}
          >
            {item.attributeName}
          </Typography>
          <Field
            name={item.order}
            label={item.attributeDescription}
            component={CustomTextarea}
          />
          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );

    case 3: //checkbox
      return (
        <Box key={item.order} marginBottom={2}>
          <Typography
            variant="caption"
            align="left"
            gutterBottom
            style={{
              fontSize: "0.60rem",
              color: "#6b6b6b",
            }}
          >
            {item.attributeName}
          </Typography>
          <Field
            name={item.order}
            label={item.attributeDescription}
            options={getSelections(item.order)}
            component={CheckboxGroup}
          />
          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );
    case 4: //radio button
      return (
        <Box key={item.order} marginBottom={2}>
          <Typography
            variant="caption"
            align="left"
            gutterBottom
            style={{ fontSize: "0.60rem", color: "#6b6b6b" }}
          >
            {item.attributeName}
          </Typography>
          <Field
            name={item.order}
            label={item.attributeDescription}
            options={getSelections(item.order)}
            component={RadioButton}
          />
          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );
    case 5: // UploadButton
      return (
        <Box key={item.order} marginBottom={2} flexDirection={"row"}>
          <Typography
            variant="caption"
            align="left"
            gutterBottom
            style={{ fontSize: "0.60rem", color: "#6b6b6b" }}
          >
            {item.attributeName}
          </Typography>
          <Field
            key={item.order}
            name={item.order}
            label={item.attributeDescription}
            component={CustomUploadFile}
          />

          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );
    case 6: //numeric box
      return (
        <Box key={item.order} marginBottom={2}>
          <Typography
            variant="caption"
            align="left"
            gutterBottom
            style={{ fontSize: "0.60rem", color: "#6b6b6b" }}
          >
            {item.attributeName}
          </Typography>
          <Field
            name={item.order}
            label={item.attributeDescription}
            component={NumericInput}
          />

          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );
    case 8: //dropdown
      return (
        <Box key={item.order} marginBottom={2} flexDirection={"row"}>
          <Typography
            variant="caption"
            align="left"
            gutterBottom
            style={{ fontSize: "0.60rem", color: "#6b6b6b" }}
          >
            {item.attributeName}
          </Typography>
          <Field
            key={item.order}
            name={item.order}
            label={item.attributeDescription}
            options={getSelections(item.order)}
            component={Dropdown}
          />

          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );
    case 9: //date picker
      return (
        <Box key={item.order} marginBottom={2} flexDirection={"column"}>
          <Typography
            variant="caption"
            align="left"
            gutterBottom
            style={{ fontSize: "0.60rem", color: "#6b6b6b" }}
          >
            {item.attributeName}
          </Typography>
          <br></br>
          <Field
            key={item.order}
            name={item.order}
            label={item.attributeDescription}
            component={CustomDatePicker}
          />

          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );
    case 10: // Single Selection Button
      return (
        <Box key={item.order} marginBottom={2} flexDirection={"row"}>
          <Field
            key={item.order}
            name={item.order}
            label={item.attributeDescription}
            options={getSelections(item.order)}
            component={SingleSelectionButton}
            selectedValue={values[item.order] ?? ""}
            onChange={(selectedOption) => {
              handleselectionvalue(selectedOption, item.attributeName);
            }}
          />

          {errors[item.order] && (
            <Typography variant="caption" color="error">
              <ErrorMessage name={item.order} />
            </Typography>
          )}
        </Box>
      );

    default:
      return null;
  }
};
